import React from 'react';

class Display extends React.Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props){
    super(props);
  }
  render(){
    return (
        <div
          className="inputDisplay" 
          id='display'>
       {this.props.currentValue}
    </div>
    );
  }
}

export default Display